<script>
import PayMethod from '@/components/PayMethod'
import Custom from '@/components/Custom'
import Success from '@/components/Success'
import options from '@/options'
export default {
  name: 'Home',
  data() {
    return {
      // 红色文字费率
      homeRate: options.homeRate,
      // 默认显示数据
      moneyList: options.moneyList,
      // 默认选中项
      active: options.moneyList[0].limit,
      // 货币类型
      currencyType: options.currencyType,
      // 点击提交按钮延时器
      countDown: null,
      // 按钮加载
      isLoading: false,
      // 是否显示弹出层
      popupsShow: false,
      // 弹出层显示那个内容
      popupsType: '',
      // 是否显示选择金额的遮罩层
      shadeShow: false
    }
  },
  methods: {
    // 点击选择金额
    selectMoney(limit, money) {
      this.active = limit
      // 存储实际应付金额
      this.$store.dispatch('setCoin', limit)
      this.$store.dispatch('setMoney', money)
    },
    // 点击自定义
    customMoney() {
      this.active = 'custom'
      this.popupsType = 'custom'
      this.popupsShow = true
      this.$store.dispatch('setMoney', 0)
    },
    // 自选，点提交
    submitFn() {
      if (!this.$store.state.money) return

      this.isLoading = true
      this.shadeShow = true
      this.popupsType = 'submit'
      this.countDown = setTimeout(() => {
        this.isLoading = false
        this.popupsShow = true
      }, options.homeDelay)
    },
    // 关闭弹出层
    doneDialog() {
      this.popupsShow = false
    },
    closeFn() {
      this.shadeShow = false
      if (this.$refs.customRef) {
        this.$refs.customRef.doneDialogFn()
      }
    },
    changePopupsType(val) {
      if (this.popupsType == 'custom') {
        this.popupsShow = false
        this.isLoading = true
        this.shadeShow = true
        setTimeout(() => {
          this.isLoading = false
          this.popupsType = 'submit'
          this.popupsShow = true
        }, options.homeDelay)
      }
    },
    // 成功回到首页
    resetFn() {
      this.popupsShow = false
      this.active = this.moneyList[0].limit
      this.$store.dispatch('setCoin', this.moneyList[0].limit)
      this.$store.dispatch('setMoney', this.moneyList[0].money)

    }
  },
  filters: {
    // 千位补逗号
    comma(val) {
      let amountStr = String(val);
      let parts = amountStr.split(".");
      let integerPart = parts[0];
      let decimalPart = parts.length > 1 ? "." + parts[1] : "";
      let reversedIntegerPart = integerPart.split("").reverse().join("");
      let integerWithCommas = reversedIntegerPart
        .match(/\d{1,3}/g)
        .join(",")
        .split("")
        .reverse()
        .join("");
      let result = integerWithCommas + decimalPart;
      return result
    }
  },
  components: { PayMethod, Custom, Success },
  mounted() {
    if (this.$route.params.msg === 'success') {
      this.popupsShow = true
      this.popupsType = 'success'
      this.$route.params.msg = ''
    }
  }
}
</script>

