<script>
import options from '@/options'
export default {
  name: 'PayMethod',
  data() {
    return {
      // 当前选择第几个付款方式
      payType: '',
      isLoading: false
    }
  },
  methods: {
    // 切换选择的付款方式
    changeType(val) {
      this.payType = val
    },
    // 点击立即付款
    submitFn() {
      this.isLoading = true
      setTimeout(() => {
        this.$router.replace('/loading')
      }, options.payMethodDelay)
    },
    // 点击关闭弹出层
    doneDialogFn() {
      this.$emit('doneDialog')
    }
  },
  filters: {
    // 千位补逗号
    comma(val) {
      let amountStr = String(val);
      let parts = amountStr.split(".");
      let integerPart = parts[0];
      let decimalPart = parts.length > 1 ? "." + parts[1] : "";
      let reversedIntegerPart = integerPart.split("").reverse().join("");
      let integerWithCommas = reversedIntegerPart
        .match(/\d{1,3}/g)
        .join(",")
        .split("")
        .reverse()
        .join("");
      let result = integerWithCommas + decimalPart;
      return result
    }
  },
}
</script>

