<script>
import options from '@/options'
export default {
  name: 'Custom',
  data() {
    return {
      currencyType: options.currencyType,
      // 用户输入的值
      coinNum: '',
      // 收集用户输入值
      coinNumList: [],
      // 按钮是否加载
      isLoading: false
    }
  },
  methods: {
    // 点击模拟按键
    clickNumberFn(e) {
      // 判断值是否大于要求的值
      if (this.coinNumList.join('') * 1 > 2500000) return
      // 如果点击的是数字
      if (!isNaN(e.target.innerHTML * 1) && e.target.innerHTML !== '') {
        if (!this.coinNumList.length && e.target.innerHTML == '000') {
          return
        } else if (e.target.innerHTML == '000') {
          this.coinNumList.push(...[0, 0, 0])
        } else {
          this.coinNumList.push(e.target.innerHTML)
        }
      }
      try {
        this.coinNum = this.coinNumList.join('') * 1 > 0 ? this.coinNumList.join('') * 1 : ''
        this.$store.dispatch('setCoin', this.coinNum)
        if (this.coinNum >= 30 && this.coinNum <= 2500000) {
          this.$store.dispatch('setMoney', (this.coinNum * options.rate).toFixed(2))
        }
        this.coinNum = this.commaFn(this.coinNum)
      } catch (error) {
        // 当删除完 不存在join
      }
    },
    // 点击删除
    deleteFn() {
      this.coinNumList.splice(-1, 1)
      try {
        this.coinNum = this.coinNumList.join('') * 1 > 0 ? this.coinNumList.join('') * 1 : ''
        this.$store.dispatch('setCoin', this.coinNum)
        if (this.coinNum >= 30 && this.coinNum <= 2500000) {
          this.$store.dispatch('setMoney', (this.coinNum * options.rate).toFixed(2))
        } else {
          this.$store.dispatch('setMoney', 0)
        }
        this.coinNum = this.commaFn(this.coinNum)
      } catch (error) {
        // 当删除完 不存在join
      }
    },
    // 补逗号
    commaFn(val) {
      let amountStr = String(val);
      let parts = amountStr.split(".");
      let integerPart = parts[0];
      let decimalPart = parts.length > 1 ? "." + parts[1] : "";
      let reversedIntegerPart = integerPart.split("").reverse().join("");
      let integerWithCommas = reversedIntegerPart
        .match(/\d{1,3}/g)
        .join(",")
        .split("")
        .reverse()
        .join("");
      let result = integerWithCommas + decimalPart;
      return result
    },
    // 关闭弹出层
    doneDialogFn(confirm) {
      if (!confirm) {
        this.$store.dispatch('setCoin', 0)
        this.$store.dispatch('setMoney', 0)
      }
      this.coinNum = ''
      this.coinNumList = []
      this.$emit('doneDialog')
    },
    // 提交
    submitFn() {
      if (this.coinNumList.join('') * 1 >= 30 && this.coinNumList.join('') * 1 <= 2500000) {
        this.doneDialogFn(true)
        this.$emit('changePopupsType', 'custom')
      }
    }
  },
  filters: {
    // 千位补逗号
    comma(val) {
      let amountStr = String(val);
      let parts = amountStr.split(".");
      let integerPart = parts[0];
      let decimalPart = parts.length > 1 ? "." + parts[1] : "";
      let reversedIntegerPart = integerPart.split("").reverse().join("");
      let integerWithCommas = reversedIntegerPart
        .match(/\d{1,3}/g)
        .join(",")
        .split("")
        .reverse()
        .join("");
      let result = integerWithCommas + decimalPart;
      return result
    }
  },
}
</script>

